<template>

<div class="tableSwapi">

  <table>


    <tr>
      <th>Name</th>
      <th>Gender</th>
      <th></th>

    </tr>

    <tr v-on:click="toggleDropdown(index)" v-bind:key="(pessoa.name)" class="teste" v-for="(pessoa,index) in pessoas">
      <td>{{pessoas[index].name}}</td>
      <td>{{pessoas[index].gender}}</td>
      <td><button style="background:none"  type="button"></button></td>
      <br><br>
      <div class="expandOng"  v-bind:id="'teste'+index"><br>


        <ul>
          <li>Birth Year: {{pessoas[index].birth_year}}</li>
          <li>Gender: {{pessoas[index].gender}}</li>
          <li>Eye Color:{{pessoas[index].eye_color}}</li><br>
          <li>Movies:</li><br>
          <li v-bind:key="(movies.name)" v-for="(movies, index) in pessoas[index].films">{{pessoas[index].films[index]}}</li><br>
        </ul>








</div>



</tr>
<tr class="endTable" style="background: #272B30">
  <th colspan="3">
    <center>
    <span>

            <input class="search" type="text" name="" value="">
            <button v-on:click="search('search')" type="button" name="button">Search</button>

    </span><br><br>



            <button v-on:click="pagina -=1;search('back')" type="button" name="button">Back</button>
            {{pagina}}
            <button v-on:click="pagina +=1;search('next')" type="button" name="button">Next</button>
        </center>
  </th>

</tr>


</table>
</div>
</template>

<script>
import $ from "jquery";
import axios from 'axios';
window.axios = require('axios');
export default {
  name: 'tableSwapi',

  data() {
    return {
      pessoas: {},
      movies: {},
      i: 0,
      pagina: 1,
      query: ""
    }
  },
  methods: {
    toggleDropdown(index) {
      $('#teste' + index).slideToggle(300);
      for (var j = 0; j < this.pessoas.length; j++) {
        console.log(j);
        for (var i = 0; i < this.pessoas[j].films.length; i++) {
          console.log(j);
          switch (this.pessoas[j].films[i]) {
            case "http://swapi.dev/api/films/1/":
              this.pessoas[j].films[i] = "A New Hope";
              break;
            case "http://swapi.dev/api/films/2/":
              this.pessoas[j].films[i] = "The Empire Strikes Back";
              break;
            case "http://swapi.dev/api/films/3/":
              this.pessoas[j].films[i] = "Return of the Jedi";
              break;
            case "http://swapi.dev/api/films/4/":
              this.pessoas[j].films[i] = "The Phantom Menace";
              break;
            case "http://swapi.dev/api/films/5/":
              this.pessoas[j].films[i] = "Attack of the Clones";
              break;
            case "http://swapi.dev/api/films/6/":
              this.pessoas[j].films[i] = "Revenge of the Sith";
              break;
            case "http://swapi.dev/api/films/7/":
              this.pessoas[j].films[i] = "A New Hope";
              break;
          }
        }
      }
    },
    search(input) {

      if (input == "search") {

        this.query = $(".search").val();
        if (this.query != "") {
          this.pagina = 1
        }
      }
      if (this.pagina < 1) {
        this.pagina = 1;
      }
      if (this.pessoas.length < 10 && this.pagina != 1 && input == "next") {
        this.pagina -= 1
      }


      axios.get("https://swapi.dev/api/people/?search=" + this.query + "&page=" + this.pagina)
        .then(response => this.pessoas = response.data.results)

    }
  },

  mounted: function allCharacters() {
    axios.get("https://swapi.dev/api/people/?search=")
      .then(response => this.pessoas = response.data.results);
    axios.get("https://swapi.dev/api/films")
      .then(response => this.movies = response.data.results)
  },
}
</script>


